.contact {
    /* border: 1px solid navy; */
    padding: 30px 10%;
   background-color: black;
   color: white;
}

.contactForm {
    /* border: 1px solid red; */
    display: flex;
}
.contact h2{
    font-weight: 500;
    color: red;
    padding: 10px 0px;
}

.contactForm>div form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 1.5px solid red;
    padding: 40px;
    border-radius: 5px;
   
}

.contactForm>div form button {
    padding: 10px 0px;
    background: linear-gradient(90deg, red 35%, #f04e3c72 100%);
    color: white;
    border: none;
    font-size: 20px;
    border: 1px solid red;
    border-radius: 3px;
}

.contactForm>div form button:hover {
    transition: all 700ms;
    color: red;
    background: none;
    border: 1px solid red;
}

.contactForm>div input {
    border: 1px solid red;
    background: none;
    outline: none;
    padding: 5px 10px;
    height: 40px;

}

.contactForm>div textarea {
    background: none;
    outline: none;
    padding: 5px 10px;
    height: 80px;
    border: 1px solid red;
    border-radius: 2px;
}

.contactForm>div {
    /* border: 1px solid green; */
    width: 50%;
}
.contactForm>div svg{
    font-size: 40px;
}
.contactForm>div:last-child {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.contactForm>div p {
    margin: 0px;
    font-size: 20px;

}

.resumeDownloadeBtn {
    border: 1px solid red;
    text-decoration: none;
    color: white;
    width: 50%;
    padding: 10px;
    background: linear-gradient(90deg, red 35%, #f04e3c72 100%);
    font-size: 20px;
    text-align: center;
}

.resumeDownloadeBtn:hover {
    transition: all 700ms;
    color: red;
    background: none;
}

.resumeDownloadbtnincontact {
    border: 1px solid red;
    text-decoration: none;
    color: white;
    padding: 10px 150px;
    background: linear-gradient(90deg, red 35%, #f04e3c72 100%);
    font-size: 20px;
    text-align: center;
    border-radius: 5px;
}

.resumeDownloadbtnincontact:hover {
    transition: all 700ms;
    color: red;
    background: none;
}

@media (max-width:900px) {
    .resumeDownloadbtnincontact {
        padding: 10px 100px;
    }
}

@media(max-width:767px) {
    .contactForm>div {
        width: 100%;
    }

    .contactForm {
        flex-direction: column;
    }

    .contactForm {
        gap: 30px;
    }

}

@media(max-width:480px) {
    .contactForm>div p {
        font-size: 15px;
    }

}

.thankyou {
    /* border: 1px solid red; */
    text-align: center;
    background: linear-gradient(90deg, red 35%, #f04e3c72 100%);
}