.About {
    padding-top: 60px;
    background-color: black
}

.background-image-about {
    background-image: url('https://wallpaperaccess.com/full/2449594.jpg');
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    /* border: 1px solid red; */
}

.fixed-container-about {
    height: 250px;
}

.aboutContent {
    border: 5px solid;
    border-image-slice: 1;
    border-width: 3px;
    border-image-source: linear-gradient(to left, red, #f04e3c72);
    padding: 30px;
    width: 90%;
    margin: auto;
    position: relative;
    bottom: 130px;
    background-color: black;
    color: white;
    border-radius: 3px;
}

.aboutContent h2 {
    font-weight: 500;
    margin-bottom: 10px;
    color: red;
}

.aboutContent p {
    font-size: large;
}

@media (max-width:420px) {
    .aboutContent h2 {
        margin-bottom: 5px;
    }

    .aboutContent p {
        font-size: small;
    }

    .aboutContent h2 {
        font-size: x-large;
    }

    .aboutContent {
        padding: 20px;
    }

    .aboutContent {
        bottom: 150px;
    }

    .About {
        padding-top: 0px;
    }


}